import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect } from 'preact/hooks';
import { InputLogic } from '../../input/inputlogic';
import ExpenseInput from '../../components/ExpenseInput';

const Health = () => {

	useEffect(() => {
		InputLogic.focusFirstInputElement()
	}, []);

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>

			<div class="container">
				<hr/>
				<h5>Health care</h5>
				Not all the expenses here apply to your situation, only check on those item that is applicable. The expense amount can be estimates. Click next when you are done.<p/>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.health.medical_insurance} title="Insurance premium" min_value="0" max_value="10000"
							description="Medical and dental insurance premium. This could be the full cost when you are buying from Marketplace or when you are employed and have to pay part of the premium."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.health.copay} title="Copay/Co-insurance" min_value="0" max_value="50000"
							description="Copay or co-insurance when visiting your medical/dental provider."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.health.out_of_pocket} title="Estimate out of pocket" min_value="0" max_value="20000"
							description="Estimate on out of pocket expenses related to healthcare."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.health.pharmacy} title="Prescription cost" min_value="0" max_value="20000"
							description="Prescription cost from pharmacies."/>					
					</div>
				</div>	
                <hr/>

				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/expense/utilities')}>Back: Utilities</button>&nbsp;
				<button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/expense/retailer')}>Next: Retailers</button>
				<hr/>                  
				<h5>Child care</h5>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.child.child_care} title="Child care" min_value="0" max_value="10000"
							description="Child care cost."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.child.tuition} title="Tuition" min_value="0" max_value="50000"
							description="This is education related. This can be tuition fees for college or tuition for K12 schools."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.child.supplies} title="Supplies" min_value="0" max_value="20000"
							description="School supplies."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.child.lunch_money} title="Lunch money" min_value="0" max_value="1000"
							description="Prescription cost from pharmacies."/>					
					</div>
				</div>	
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.child.classes_fee} title="Sports fee" min_value="0" max_value="5000"
							description="Classes like art, dance, tae-kwon-doe, soccer etc."/>
					</div>
                    <div class="col">
						<ExpenseInput store={Stores.child.student_loan} title="Student loan payment" min_value="0" max_value="5000"
							description="Student loan payment for someone in your household."/>
					</div>                    
				</div>	                
			</div>
		</div>
		
	);
};

export default Health;
